@font-face {
  font-family: 'Spoqa';
  font-style: normal;
  font-weight: 400;
  src: url(assets/fonts/Spoqa/Spoqa-Regular.eot);
  src: url(assets/fonts/Spoqa/Spoqa-Regular.eot?#iefix)
      format('embedded-opentype'),
    url(assets/fonts/Spoqa/Spoqa-Regular.woff) format('woff'),
    url(assets/fonts/Spoqa/Spoqa-Regular.ttf) format('truetype');
  src: local(※), url(assets/fonts/Spoqa/Spoqa-Regular.woff) format('woff');
}

@font-face {
  font-family: 'Spoqa';
  font-style: normal;
  font-weight: 800;
  src: url(assets/fonts/Spoqa/Spoqa-Bold.eot);
  src: url(assets/fonts/Spoqa/Spoqa-Bold.eot?#iefix) format('embedded-opentype'),
    url(assets/fonts/Spoqa/Spoqa-Bold.woff) format('woff'),
    url(assets/fonts/Spoqa/Spoqa-Bold.ttf) format('truetype');
  src: local(※), url(assets/fonts/Spoqa/Spoqa-Bold.woff) format('woff');
}

@font-face {
  font-family: 'Yj';
  font-style: normal;
  font-weight: 400;
  src: url(assets/fonts/YangJin/YJ.eot);
  src: url(assets/fonts/YangJin/YJ.eot?#iefix) format('embedded-opentype'),
    url(assets/fonts/YangJin/YJ.woff) format('woff'),
    url(assets/fonts/YangJin/YJ.ttf) format('truetype');
  src: local(※), url(assets/fonts/YangJin/YJ.woff) format('woff');
}

@font-face {
  font-family: 'Spotlight';
  font-style: normal;
  font-weight: 400;
  src: url(assets/fonts/Spotlight/Spotlight.eot);
  src: url(assets/fonts/Spotlight/Spotlight.eot?#iefix)
      format('embedded-opentype'),
    url(assets/fonts/Spotlight/Spotlight.woff) format('woff'),
    url(assets/fonts/Spotlight/Spotlight.ttf) format('truetype');
  src: local(※), url(assets/fonts/Spotlight/Spotlight.woff) format('woff');
}
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  letter-spacing: -0.6px;
  color: #3c3c3c;
  font-family: 'Spoqa', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
